.container{
    width: 100%;
    height: 100vh;
    position: relative;
    align-items: center;
    object-fit:cover;
}

.container .img_fluid{
    width: 100%;
    height: 100%;
    opacity: 0.5;
}



.center_element{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}
.center_element h1{
    color: rgb(34, 33, 33);
    font-weight: 900;
    font-size: 50px;
    letter-spacing: 1px;
}

.center_element p{
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 20px;
    /* font-family: serif; */
}

.btn_but{
    width: 200px;
    height: 40px;
    /* border: 1px solid rgb(255, 255, 255); */
    outline: none;
    text-decoration: none;
    font-size: 20px;
    border-radius: 5px;
    padding: 5px;
    box-shadow: 0.5px 0.5px 5px black,0px 0px 0px black;
}

.btn_but:hover{
    background-color: rgb(82, 168, 238);
    color: white;
}
.center_element h1:hover{
   color: rgb(53, 40, 40);
}




@media (max-width: 998px) {
    .center_element h1{
        font-size: 35px;
    }
    .center_element p{
        font-size: 20px;
    }
    img{
        width: 100px;
        height: 100px;
        object-fit:cover;
    }
}

@media (max-width: 798px) {
    .center_element h1{
        font-size: 30px;
    }
    
}

@media (max-width: 520px) {
    .center_element h1{
        font-size: 25px;
    }
}

@media (max-width: 400px) {
    .center_element h1{
        font-size: 15px;
    }
    .center_element p{
        font-size: 10px;
    }

}
.nav_bar{
    width: 100%;
    height: 4rem;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 200px;
    padding-right: 30px;
    /* position: fixed; */
    position: sticky;
    top: 0;
    z-index: 1;
}

.Navbar_brand{
    text-decoration: none;
    font-size: 2rem;
    font-weight: 500;
    font-family: 'Roboto', sans-serif;
}

.list{
    list-style-type: none;
    display: flex;
}


.right_Nav_bar .ham_burger{
    display: none;
}

.Navbar_brand:hover{
    color: rgb(70, 110, 243);
}

.list a{
    margin-left: 2rem;
    text-decoration: none;
    font-size: 1rem;
}

.list a:hover{
    color: green;
}
.active_class{
    color: blue;
    border-bottom: 1.5px solid rgb(131, 98, 98);
}





