@media (max-width: 1080px) {
    .nav_bar{
        padding-left: 70px;
    }
     .Navbar_brand{
        font-size: 30px;
    }
     .right-element{
        display: none;
    }
    .right_Nav_bar .ham_burger{
        display: block ;
        font-size: 30px;
    }
}


@media (max-width: 1079px) {
    .nav_bar{
        padding-left: 50px;
    }
     .Navbar_brand{
        font-size: 20px;
    }
    .right-element{
        display: none;
    }
    .ham_burger{
        margin-left: 50px;
    }
    .right_Nav_bar .ham_burger{
        margin-left: 50px;
        display: block ;
        font-size: 30px;
    }
    .ham_burger{
        position: absolute;
        right: 20px;
        top: 20px;
    }
    .mobile-menu-link{
        grid-column: 2/4;
        position: relative;
        z-index: 99;
       
    }

    .mobile-menu-link{
        position: absolute;
        top: 60px;
        right: 0px;
        background-color: rgb(255, 255, 255);
        height: 15rem;
        display: grid;
        width: 100%;
        align-items: center;
        padding-left: 3rem;
        -webkit-transform-origin: top;
                transform-origin: top;
    }
    .right-element .list{
        display: flex;
        flex-direction: column;

    }
    .right-element .list li{
        margin-top: 10px;
        color: white;
        font-size: 25px;
    }
    /* .list{
        display: none;
    } */
    
}

